body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #F4F4F4 white;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: white;
}

*::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom right, #FBD7FF 10%, #FFDEC1 80%);
  border-radius: 20px;
  border: 3px solid white;
}
.scroll {
	height: calc(100vh - 14rem);
}

.bg-1 {
	background-image: linear-gradient(to bottom right, #FBD7FF 10%, #FFDEC1 80%);
	margin: 1rem 1rem 0.5rem;
	border-radius: 0.5625rem;

}

.header {
	border-radius: 0.5625rem;
    box-shadow: 0 1.5rem 2rem -0.5rem rgb(0 0 0 / 7%), 0 0.25rem 2rem 0 rgb(0 0 0 / 3%);
    background-color: #FFFFFF;
	display: flex;
	justify-content: space-between;
	width: 80%;
	margin: 0 auto;
	align-items: center;
	border-radius: 0.5rem;
}

@media only screen and (max-width: 500px) {
	.header {
		display: block;
		text-align: center;
	}

	div.header > div > h1 {
		justify-content: space-between;
		padding: 0rem 0rem 1rem;
	}
	input {
		display: block;
		width: 100%;
	}

	.scroll {
		height: calc(100vh - 17.55rem);
	}	

	.footer-body {
		font-size: 13px !important;
	}
}

div.header > div > h1 {
	display: flex;
	align-items: center;
	/*font-size: 72px;*/
	/*background: -webkit-linear-gradient(#FBD7FF 10%, #FFDEC1 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;*/
	color: black;
}

input {
	border-radius: 0.3rem;
}

.main {
	border-radius: 0.5625rem;
    box-shadow: 0 1.5rem 2rem -0.5rem rgb(0 0 0 / 7%), 0 0.25rem 2rem 0 rgb(0 0 0 / 3%);
    background-color: #FFFFFF;
	display: flex;
	justify-content: space-between;
	width: 80%;
	margin: 0 auto;
	align-items: center;
	border-radius: 0.5rem !important;
}

.bg-gray-1 {
	background-color: #F4F4F4;
}

.footer {
	background: #000;
	margin: 0rem 1rem;
	border-radius: 0.5625rem;
	color: white;
}

.footer-body {
	display: flex;
	justify-content: space-between;
	width: 80%;
	margin: 0 auto;
	align-items: center;
}



